import React,{Component} from 'react';
import axios from 'axios';
import { Modal, Button, Input, message } from 'antd';
import {Link, withRouter} from 'react-router-dom';


class Login extends Component{
    constructor(props){
        super(props);
        this.state={
            visible: false,
            login: false,
            user: "",
            password: ""
        }
    }
    showModal(){
        this.setState({
            visible: true
        })
    }
    hideModal(){
        this.setState({
            visible: false
        })
    }
    checkLogin(){
        const { user, password }=this.state;
        const url=`http://www.dell-lee.com/react/api/Login.json?user=${user}&password=${password}`;
        axios.get(url,{
            withCredentials: true
        }).then(res => {
            const login2=res.data.data.login;
            if(login2){
                message.success("登陆成功");
                this.setState({
                    visible: false,
                    login: true
                })
            }else{
                message.error("登陆失败");
            }
        })
    }
    changUser(e){
        this.setState({
            user: e.target.value
        })
    }
    changPassword(e){
        this.setState({
            password: e.target.value
        })
    }
    loginOut(){
        axios.get("http://www.dell-lee.com/react/api/logout.json",{
            withCredentials: true
        }).then(res => {
            const data=res.data.data;
            if(data.logout){
                this.setState({
                    login: false
                })
                this.props.history.push('/');
            }
        })
    }
    render(){
        const login=this.state.login;
        return(
            <div style={{marginBottom: 10}}>
                {
                    login ?
                    <Button type="primary" onClick={this.loginOut.bind(this)}>退出</Button> :
                    <Button type="primary" onClick={this.showModal.bind(this)}>登陆</Button>
                }
                <Link to='/Vip'>
                    <Button type="primary" style={{marginLeft: 10}}>VIP</Button>
                </Link>
                <Modal
                title="登陆"
                visible={this.state.visible}
                onOk={this.checkLogin.bind(this)}
                onCancel={this.hideModal.bind(this)}
                >
                    <Input placeholder="请输入用户名" style={{marginBottom: 10}} value={this.state.user} onChange={this.changUser.bind(this)}/>
                    <Input.Password placeholder="请输入密码" value={this.state.password} onChange={this.changPassword.bind(this)}/>
                </Modal>
            </div>
        )
    }
    componentDidMount(){
        axios.get("http://www.dell-lee.com/react/api/islogin.json",{
            withCredentials: true
        }).then(res => {
            this.setState({
                login: res.data.data.login
            })
        })
    }
}

export default withRouter(Login);