import React, {Component, Fragment} from 'react';
import { Menu, Icon } from 'antd';
import {Link} from 'react-router-dom';

import axios from 'axios';
import './style.css';
import logo from './logo.png';

class AppHeader extends Component{
    componentDidMount(){
        axios.get('http://www.dell-lee.com/react/api/header.json').then(res => {
            this.setState({
                list: res.data.data
            })
        })
    }

    constructor(props){
        super(props);
        this.state={
            list: []
        }
    }

    getMenu(){
        return this.state.list.map(item => {
            return (
                <Menu.Item key={item.id}>
                    <Link to={`/${item.id}`}>
                        <Icon type={item.icon} />{item.title}
                    </Link>
                </Menu.Item>
            )
        })
    }

    getHome(){
        window.location.href="http://localhost:3000";
    }
    render(){
        return (
            <Fragment>
                <img src={ logo } className="app-header-logo" alt="logo" onClick={this.getHome} style={{cursor: "pointer"}}/>
                <Menu onClick={this.handleClick} mode="horizontal" className="app-header-menu">
                    {this.getMenu()}
                </Menu>
            </Fragment>
        )
    }
}

export default AppHeader;